import React, {Component} from 'react'
import './ArticleImage.css'
class ArticleImage extends Component {
    render () {
        return (
                    <img className=" imageContainer col-md-12 col-sm-12 col-xs-12" src={this.props.src}/>
        )
    }
}

export default ArticleImage


