import React, {Component} from 'react'


class Article extends Component {
  render () {
    return (
      <div className="blog col-md-8 col-md-offset-2">
      <article className="blog col-md-10 col-md-offset-1"> 
        {this.props.children}       
      </article>
      </div>
    )
  }
}


export default Article


