import React, {Component} from 'react'
import './ArticleTime.css'
class ArticleTime extends Component {
    
    
    render () {
        return (
            <time dateTime={this.props.dateTime} className="article_time">October 16, 2013</time>   
        )
    }
}

export default ArticleTime


