	var para1 = "I am a native developer I have developed for both IOS and Android . Recently I developed couple of app in react native and I loved it . Below I will share the queries I had though out the process : ";
	var header1 = "First thing first";
	var content1 = "React native is great. The site you are on right now is also built using ReactJS only. The programing Model of react js is extremly cool. Lets Have a look at the code ";
	
	var code = "";
	code += "    class Drawer extends Component {";
	code += "\n" + "        render () {";
	code += "\n" + "                var navigationView = (";
	code += "\n" + "                    <View style={{flex: 1, backgroundColor: '#fff'}}>";
	code += "\n" + "                    <Text style={{margin: 10, fontSize: 15, textAlign: 'left'}}>I'm in the Drawer!<\/Text>";
	code += "\n" + "                    <\/View>";
	code += "\n" + "                );";
	code += "\n" + "                return (";
	code += "\n" + "                    <DrawerLayoutAndroid";
	code += "\n" + "                    drawerWidth={300}";
	code += "\n" + "                    drawerPosition={DrawerLayoutAndroid.positions.Left}";
	code += "\n" + "                    renderNavigationView={() => navigationView}>";
	code += "\n" + "                    <View style={{flex: 1, alignItems: 'center'}}>";
	code += "\n" + "                        <Text style={{margin: 10, fontSize: 15, textAlign: 'right'}}>Hello<\/Text>";
	code += "\n" + "                        <Text style={{margin: 10, fontSize: 15, textAlign: 'right'}}>World!<\/Text>";
	code += "\n" + "                    <\/View>";
	code += "\n" + "                    <\/DrawerLayoutAndroid>";
	code += "\n" + "                )";
	code += "\n" + "        }";
	code += "\n" + "    }";
	
	var content1_1 = "this is the code to create view with implementation of navigation drawer which on android will take hundreads of lines of code and very hard to implement ";
	
	var header2 = "How does it works / what does native means ";
	var content2 = 'In react native most of the time you application is going to be js application only but unlike hybrid apps like xcodexcordova\/phonegapxcodexwhich only runs in a web view using xcodexHTMLxcodex and xcodexCSSxcodex with some native api support react native actually is kind of wrapper over native component much like a xamarin app . Don\'t worry if you don\'t know about these technologies this was just for reference ';
	
	var content3 = "So what  native means when you run your react native app javascript will create a native view for every element you define in jsx tag for example xcodex<TextInput>xcodex wraps a xcodexandroid.widget.EditTextxcodex on android and a xcodexUIKit.UITextField xcodex on IOS. But here is the thing react do not always wrap a corresponding native element but wraps a generic view/or a custom native implemented view and adds properties to it. Like xcodex<Text> xcodex or xcodex<Switch> xcodex does not actually wraps a xcodexandroid.widget.TextView/android.widget.Switch xcodex or IOS xcodexUIText/UISwitchxcodex but its kind of Wraps a generic view (xcodexandroid.viewxcodex on android xcodexUIViewxcodex on IOS).";
	
	var content4 = "React also wraps some platform specific components like xcodex<ToolBarAndroid>xcodex xcodex<DrawerLayoutAndroid>xcodex for android only and xcodex<TabBarIOS>xcodex and xcodex<NavigationBarIOS>xcodex for IOS only.These component usually wraps corresponding native Component like xcodexDrawerlayout/ToolBarxcodex in android and xcodexUINavigator/UITabsxcodex in IOS.";
	
	var header5 = "The Layout";
	var content5 = "Unlike native development where we use xcodexXML xcodexon android xcodexStoryBoard xcodexon IOS react native uses xcodexflexbox xcodexfor Layout and It works amazingly well. If you know xcodexCSS xcodexyou already are master at flex-box. With flexbox you can do almost any Layout but sometime it works differently on platform for example setting xcodexborder-radius xcodexfor xcodex<InputText>xcodexdoesn't work on Android. React provide nice api for acene transition and costome animation that works well.";
	
	var header6 = "Performance";
	var content6 = "Javascript perform amazingly well on IOS devices but works not so good on Android devices specially old one. React app perform poorly when in debugging mode but Performance increases drastically in release mode because react has to do a lot of work to give us nice debugging exprience. Thr Performance is not as good as native app . Although it gives us 60 fps as the javascript runs in a different thread but view-intraction still stucks if javascript thread is doing a lot of work.";
	
	var content7 = "If your view is very nested like with tabs/drawer/lists in a single screen than you will see a lag in app while loading view. xcodex<ListView>xcodexon react sucks it dont even recycle view that goes off screen both the native platform has very optimised implementation of xcodexListView/UITableViewxcodex. There in no wrapper available for native xcodexRecyclerView(android)xcodex or xcodexUITableView(IOS) xcodex.If your list size is as large as thousands than your app might crash if user scroll till bottom. React only renders few rows at first but as user scroll it keeps adding view to list instead recyle which results in huge memory consuption.Also react isn't fast enought to render rows as fast as as you scroll down so you will many time hit end of list and than react will render few rows the lag really kills th user exprience. Although react promises they are working on recylable implemetation of List/Table view but if you app uses complex nested view you should not consider using react not yet.";
	
	var content8 = "This is graph showing memory increase as the list scroll. This image was posted on github comunity of react-native";
	
	var header9 = "The joy of Developing in JavaScript";
	var content9 = "#1. Developing with react native is super awesome. React-native cli now provide direct running app in IOS Simulator/Android Emulator without even touching xcode/Android Studio. React provide feature like xboldxHot Reloading , Live Reloadxboldx and xboldxChrome/IDE Debuggerxboldx which makes debuging alot easier";
	var content10 = "#2. In native development even for a smallest change you have to recompile the whole project that takes a lot of time where as In react native you just press save and changes will hot reload or patch based on your development setting.";
	var content11 = "#3. xboldxWireless debuggingxboldx is even more fun to work with if your mobile device and development machine are on same network than you can debug app on your mobile wirelessly just hit save and hot reload.";
	var content12 = "#4. xboldxOn Scren Logxboldx and xboldxBreakPointxboldx are also cool feature of react native when ever app has issue it shows log on mobile screen itself. You can put debbuger in code to stop code exection and see all the values of objects in chrome debugger or you can also use IDE like xboldxVS Codexboldx with plugins to run and debug in ide itself.";
	
	var content13 = "#5. xboldxPackage Managerxboldx is powerfull tool for react native there are thousands of implementation of react component available just hit xcodexnpm install myAwesomeLibraryxcodex and you have it. The react way is making  app is create reusable component.";
	
	var header14 = "Conclusion";
	var content14 = "React is awesome and will evolve with time. Learing and developing app is lot easier than native platform indivisually.  Read the xboldxPerformacexboldx section carefully before deciding to use it";
	
