import React, {Component} from 'react'
import './ArticleContent.css'
class ArticleContent extends Component {
    
     constructor(props) {
         super(props);
         this.tokens = this.props.value.split('xcodex')
   }
    
   

    render () {
        return (
            <p className="content">
             {
                 this.tokens.map((val , i) => {
                     if(i%2 == 0)
                     {
                       return (<span>{
                           val.split("xboldx").map((val2 , i2) => {
                               if(i2%2 ==0)
                               {
                                   return(<span>{val2}</span>)
                               }else
                                {
                                    return (<span className="bold">{val2} </span>)
                                 }
                           })
                       }</span>)
                     }else
                     {
                         return (<code className="pretty">{val}</code>)
                     }
                 })
             }
            </p>
        )
    }
}

export default ArticleContent


